import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subject, timer } from 'rxjs';
import { map, takeUntil } from 'rxjs/operators';
import { ApiService } from 'src/app/shared/api.service';
import { NotificationService } from 'src/app/shared/notification.service';

const states = {
  New: {
    class: 'red',
    options: ['Order accepted'],
    'Order accepted': 'Preparing',
  },
  Preparing: {
    class: 'yellow',
    options: ['Order completed'],
    'Order completed': 'Ready for Pickup',
  },
  'Ready for Pickup': {
    class: 'green',
    options: ['Order collected'],
    'Order collected': 'Collected',
  },
  Collected: {},
  Cleared: {},
};

const _message = (orderitem, option) => {
  switch (option) {
    case 'Accept':
      return `Your orderitem ${orderitem.product.name} of ${
        orderitem.parent.ref
      } is being prepared. We will inform you when it is ready for collection.`;
    case 'Complete':
      return `Your orderitem ${orderitem.product.name} of ${
        orderitem.parent.ref
      } is ready for pickup. Please approach counter now.`;
    case 'Collected':
      return `Your orderitem ${orderitem.product.name} of ${
        orderitem.parent.ref
      } has been collected. Enjoy !`;
    default:
      return '';
  }
};

const fbcommand = async (api, order, command) => {
  await api
    .post(`/api1/fbcommand`, {
      pageid: order.pageid,
      fbid: order.fbid,
      commands: [command],
    })
    .toPromise();
};

@Component({
  selector: 'app-kds',
  templateUrl: './kds.component.html',
  styleUrls: ['./kds.component.css'],
})
export class KdsComponent implements OnInit, OnDestroy {
  orderitems = [];

  private _ngDestroy$ = new Subject<void>();

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router
  ) {}

  async ngOnInit() {
    // await this.load();

    timer(0, 10000)
      .pipe(takeUntil(this._ngDestroy$))
      .subscribe(async () => {
        await this.load();
      });
  }

  ngOnDestroy() {
    this._ngDestroy$.next();
    this._ngDestroy$.complete();
  }

  async load() {
    this.orderitems = await this.api
      .get(`/api3/orderitems/kds`)
      .pipe(
        map(orderitems =>
          (<any[]>orderitems).map(order =>
            order.kdsstatus ? order : { ...order, kdsstatus: 'New' }
          )
        ),
        map(orders => orders.reverse())
      )
      .toPromise();
  }

  async act(orderitem, option) {
    orderitem.kdsstatus = states[orderitem.kdsstatus][option];

    await this.api.post(`/api3/orderitems`, orderitem).toPromise(); // save

    await fbcommand(this.api, orderitem, {
      type: 'TEXT',
      payload: _message(orderitem, option),
    });

    if (option === 'Complete') {
      await fbcommand(this.api, orderitem, {
        type: 'COLLECTION_SLIP',
        payload: orderitem.id,
      });
    }

    // if (option === 'Collect') {
    //   this.pastorders.push(order);
    //   this.orders = this.orders.filter(o => o.id !== order.id);
    // }
  }

  async clear(orderitems) {
    if (
      confirm(
        `Warning. This action will clear orders [${orderitems
          .map(order => order.ref)
          .join()}] from the KDS and is not reversible.`
      )
    ) {
      for (const orderitem of orderitems) {
        orderitem.kdsstatus = 'Cleared';
        await this.api.post(`/api3/orderitems`, orderitem).toPromise(); // save
      }
    }
  }

  filter(orderitems, kdsstatus) {
    return orderitems.filter(order => order.kdsstatus === kdsstatus);
  }

  options(orderitem) {
    return states[orderitem.kdsstatus].options;
  }

  class(orderitem) {
    return states[orderitem.kdsstatus].class;
  }
}
